import React, { useEffect, useState } from "react";
import MyNavBar from "../../common/MyNavBar";
import MyHttp from "../../helper/MyHttp";
import MyInput from "../../common/MyInput";
import { Modal, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { DesignersI } from "./add-fashion-designer";
import EventI from "../event/event-interface";

interface ViewDesignerI extends DesignersI {
  data: DesignersI[];
  outfit_type_text: string;
  show: boolean;
  eventShow: boolean
  allEvents: EventI[]
  selectedEventId: number
  error: string;
}

class ViewFashionDesigner extends React.Component<any, ViewDesignerI> {
  constructor(props: any) {
    super(props);

    this.state = {
      data: [],
      outfit_type_text: "",
      id: this.props.match.params.id,
      brand_name: "",
      designer_name: "",
      contact_number: "",
      email: "",
      start_price: 0,
      end_price: 0,
      outfit_types: [],
      speciality: "",
      location: "",
      insta_link: "",
      fb_link: "",
      start_working_hours: "",
      end_working_hours: "",
      vendor_id: 0,
      events: [],
      allEvents: [],
      show: false,
      eventShow: false,
      selectedEventId: 0,
      error: ""
    };

    this.handleClose = this.handleClose.bind(this);
    this.saveData = this.saveData.bind(this);
    this.addEvent = this.addEvent.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onSelectChange = this.onSelectChange.bind(this);
    this.onOutfitAdded = this.onOutfitAdded.bind(this);
    this.renderOutfits = this.renderOutfits.bind(this);
  }

  async getAllEvents(){
      try {
          const res = await MyHttp.get(`/api/event`);
          if(res.status === 200){
              const data: EventI[] = res.data
              if(data !== null || data !== undefined){
                  this.setState({
                      allEvents: data
                  })
              }
          }
      } catch (error) {
          console.log(`Error Events`);
          
      }
  }

  async getData() {
    try {
      const res = await MyHttp.get(`/api/designer/${this.state.id}`);
    //   const res2 = await MyHttp.get(`/api/designer/getEvents`);
    //   console.log(res2.data);
      
    
      if (res.status === 200) {
        console.log(res.data);
        if (res.data !== null) {
          const data: DesignersI = res.data;
          this.setState({
            brand_name: data.brand_name,
            designer_name: data.designer_name,
            contact_number: data.contact_number,
            email: data.email,
            start_price: data.start_price,
            end_price: data.end_price,
            outfit_types: data.outfit_types,
            speciality: data.speciality,
            location: data.location,
            insta_link: data.insta_link,
            fb_link: data.fb_link,
            start_working_hours: data.start_working_hours,
            end_working_hours: data.end_working_hours
          });
        }
      }
    } catch (error) {
      this.setState({
        error: error.message
      });
    }
  }

  setEditData(id: number) {
    this.setState({
      id: id
    });
    const gotData: DesignersI | undefined = this.state.data.find(
      (d: DesignersI) => d.id === id
    );
    if (gotData === undefined || gotData === null) {
      console.log("Wrong Id");
      return;
    }
  }

  async saveData() {
    try {
      const {
        id,
        brand_name,
        designer_name,
        contact_number,
        email,
        start_price,
        end_price,
        outfit_types,
        speciality,
        location,
        insta_link,
        fb_link,
        start_working_hours,
        end_working_hours,
        vendor_id
      } = this.state;

      const dataToSend: DesignersI = {
        id,
        brand_name,
        designer_name,
        contact_number,
        email,
        start_price,
        end_price,
        outfit_types,
        speciality,
        location,
        insta_link,
        fb_link,
        start_working_hours,
        end_working_hours
      };
      if (outfit_types.length === 0) {
        this.setState({
          error: "Enter atleat one outfit type."
        });
        return;
      }
      const res = await MyHttp.put("/api/designer", dataToSend);
      const data: DesignersI = res.data;
      this.setState({
        brand_name: data.brand_name,
        designer_name: data.designer_name,
        contact_number: data.contact_number,
        email: data.email,
        start_price: data.start_price,
        end_price: data.end_price,
        outfit_types: data.outfit_types,
        speciality: data.speciality,
        location: data.location,
        insta_link: data.insta_link,
        fb_link: data.fb_link,
        start_working_hours: data.start_working_hours,
        end_working_hours: data.end_working_hours,
        show: false
      });
      console.log(this.state.data);
    } catch (error) {
      console.log(error.message);
    }
  }

  async addEvent(){
      try {
          const {id, selectedEventId} = this.state
          const data = {
              event_id: selectedEventId,
              designer_id: id!
          }
          const res = await MyHttp.post("/api/designer/addEvent", data);
          if(res.status === 200){
              console.log(res.data);
          }
      } catch (error) {
        console.log(error);
      }
  }

  renderEvents() {
    const out = (this.state.events || []).map((d: EventI, i: number) => {
      const linkTo = `/designer/view/${d.id}`;
      return (
        <tr key={d.id!}>
          <td>{i + 1}</td>
          <td>
            {" "}
            <Link to={linkTo}> {d.name} </Link>{" "}
          </td>
          <td>{d.start_date}</td>
          <td>
            <button
              className="btn btn-warning"
              onClick={() => {
                this.setEditData(d.id!);
              }}
            >
              Edit
            </button>
          </td>
        </tr>
      );
    });
    return out;
  }

  componentDidMount() {
    this.getData();
  }

  handleClose() {
    this.setState({
      show: false,
      eventShow: false
    });
  }

  onChange(e: React.ChangeEvent<HTMLInputElement>) {
    console.log(e.currentTarget.value);

    this.setState({
      ...this.state,
      [e.currentTarget.name]: e.currentTarget.value
    });
  }

  onSelectChange(event: React.SyntheticEvent<HTMLSelectElement, Event>) {
    this.setState({
        selectedEventId: parseInt(event.currentTarget.value)
    })
  }

  onOutfitAdded(event: React.KeyboardEvent<HTMLInputElement>) {
    if (event.keyCode === 13) {
      const { outfit_type_text, outfit_types } = this.state;
      outfit_types.push(event.currentTarget.value);
      this.setState({
        outfit_types
      });
      event.currentTarget.value = "";
      event.preventDefault();
    }
  }

  renderOutfits(): React.ReactNode {
    const { outfit_types } = this.state;
    const out = outfit_types?.map((ot: string, i: number) => {
      return (
        <li key={ot}>
          {ot}{" "}
          <button
            className="btn btn-sm btn-secondary"
            onClick={() => {
              outfit_types.splice(i, 1);
              this.setState({
                outfit_types
              });
            }}
          >
            {" "}
            -{" "}
          </button>
        </li>
      );
    });
    return out;
  }

  render() {
    return (
      <React.Fragment>
        <MyNavBar />
        <div className="container">
          <div className="jumbotron">
            <div className="d-inline-block">
              <h2>View/Edit</h2>
            </div>
            <div style={{ marginLeft: "20px" }} className="d-inline-block">
              <button
                className="btn btn-warning"
                onClick={() => {
                  this.setState({
                    show: true
                  });
                }}
              >
                Edit
              </button>
            </div>
            <Modal show={this.state.show} onHide={this.handleClose}>
              <Modal.Header closeButton>
                <Modal.Title>Edit</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <MyInput
                  label="Brand Name"
                  value={this.state.brand_name}
                  name="brand_name"
                  onChange={this.onChange}
                />
                <MyInput
                  label="Designer Name"
                  value={this.state.designer_name}
                  name="designer_name"
                  onChange={this.onChange}
                />
                <MyInput
                  label="Contact Number"
                  value={this.state.contact_number}
                  name="contact_number"
                  onChange={this.onChange}
                />
                <MyInput
                  label="Email"
                  value={this.state.email}
                  name="email"
                  onChange={this.onChange}
                />
                <MyInput
                  label="Start Price"
                  value={this.state.start_price.toString()}
                  name="start_price"
                  onChange={this.onChange}
                />
                <MyInput
                  label="End Price"
                  value={this.state.end_price.toString()}
                  name="end_price"
                  onChange={this.onChange}
                />

<label>Outfit Types:</label>
              <select
                onChange={this.onSelectChange}
                name="services"
                className="custom-select"
                multiple
              >
                <option value="Traditional wear">Bridal Makeup</option>
                <option value="Party wear">Full Makeup</option>
                <option value="Casuals">Face Makeup</option>
              </select>

              <label>Speciality:</label>
              <select
								onChange={this.onSelectChange}
                name="speciality"
                className="custom-select"
                multiple
              >
                <option value="Traditional wear">Bridal Makeup</option>
                <option value="Party wear">Full Makeup</option>
                <option value="Casuals">Face Makeup</option>
              </select>
                <MyInput
                  label="Location"
                  value={this.state.location}
                  name="location"
                  onChange={this.onChange}
                />
                <MyInput
                  label="Insta Link"
                  value={this.state.insta_link}
                  name="insta_link"
                  onChange={this.onChange}
                />
                <MyInput
                  label="Fb Link"
                  value={this.state.fb_link}
                  name="fb_link"
                  onChange={this.onChange}
                />
                <MyInput
                  label="Start Working Hours"
                  value={this.state.start_working_hours.toString()}
                  name="start_working_hours"
                  onChange={this.onChange}
                />
                <MyInput
                  label="End Working Hours"
                  value={this.state.end_working_hours.toString()}
                  name="end_working_hours"
                  onChange={this.onChange}
                />
              </Modal.Body>
              <Modal.Footer>
                <Button variant="secondary" onClick={this.handleClose}>
                  Close
                </Button>
                <Button variant="primary" onClick={this.saveData}>
                  Save Changes
                </Button>
              </Modal.Footer>
            </Modal>
            <h3>Brand Name</h3>
            <p>{this.state.brand_name}</p>

            <h3>Designer Name</h3>
            <p>{this.state.designer_name}</p>

            <h3>Contact Number</h3>
            <p>{this.state.contact_number}</p>

            <h3>Email</h3>
            <p>{this.state.email}</p>

            <h3>Start Price</h3>
            <p>{this.state.start_price}</p>

            <h3>End Price</h3>
            <p>{this.state.end_price}</p>

            <h3>Outfit Types</h3>
            <p>{this.state.outfit_types.join(", ")}</p>

            <h3>Speciality</h3>
            <p>{this.state.speciality}</p>

            <h3>Location</h3>
            <p>{this.state.location}</p>

            <h3>Insta Link</h3>
            <p>{this.state.insta_link}</p>

            <h3>Fb Link</h3>
            <p>{this.state.fb_link}</p>

            <h3>Start Working Hours</h3>
            <p>{this.state.start_working_hours}</p>

            <h3>End Working Hours</h3>
            <p>{this.state.end_working_hours}</p>
          </div>
          <Modal show={this.state.eventShow} onHide={this.handleClose}>
            <Modal.Header closeButton>
              <Modal.Title>Edit</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <select className="custom-select" onChange={this.onSelectChange}>
                  {(this.state.allEvents || []).map((e: EventI) => {
                      return <option key={e.id!} value={e.id!}>{e.name}</option>
                  })}
              </select>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={this.handleClose}>
                Close
              </Button>
              <Button variant="primary" onClick={this.addEvent}>
                Add Event
              </Button>
            </Modal.Footer>
          </Modal>
          <h2 className="d-inline">Events Going TO:</h2>
          <button onClick={() => {
              if(this.state.allEvents.length === 0){
                  this.getAllEvents()
              }
              this.setState({
                  eventShow: true
              })
          }} className="d-inline btn btn-sm btn-success">+</button>
          <p className="text-danger">{this.state.error}</p>
          <table className="table">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">Event Name</th>
                <th scope="col">Event On</th>
              </tr>
            </thead>
            <tbody>{this.renderEvents()}</tbody>
          </table>
        </div>
      </React.Fragment>
    );
  }
}

export default ViewFashionDesigner;
