import React, { Component, FormEvent } from "react";

import MyNavBar from "../../common/MyNavBar";
import MyInput from "../../common/MyInput";
import MyHttp from "../../helper/MyHttp";
import MyTextArea from "../../common/MyTextarea";

export interface MakeupArtistI {
  id?: number | null;
  name: string;
  description: string;
  contact_number: string;
  email: string;
  start_price: number;
  end_price: number;
  services: string[];
  speciality: string[]
  location: string;
  insta_link?: string;
  fb_link?: string;
  start_working_hours: Date | string;
  end_working_hours: Date | string;
  photo_ids?: number[];
  approved?: boolean;
  vendor_id: number;
}
interface AddMakeupartistI extends MakeupArtistI {
  error: string;
  message: string;
}

export default class AddMakeupartist extends Component<any, AddMakeupartistI> {
  constructor(props: any) {
    super(props);

    this.state = {
      id: 0,
      name: "",
      description: "",
      contact_number: "",
      email: "",
      start_price: 0,
      end_price: 0,
      services: [],
      speciality: [],
      location: "",
      insta_link: "",
      fb_link: "",
      start_working_hours: "",
      end_working_hours: "",
      vendor_id: this.props.match.params.id,
      error: "",
      message: ""
    };
    this.saveData = this.saveData.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onSelectChange = this.onSelectChange.bind(this);
  }

  onChange(e: React.ChangeEvent<HTMLInputElement>) {
    this.setState({
      ...this.state,
      [e.currentTarget.name]: e.currentTarget.value
    });
  }

  onSelectChange(event: React.SyntheticEvent<HTMLSelectElement, Event>) {
    const data = event.currentTarget.value;
    const name = event.currentTarget.name;
    let arr;
    console.log(name);
    console.log(data);

    if (name === "services") {
      console.log("1");

      arr = this.state.services;
    } else {
      arr = this.state.speciality
    }
    if (arr !== undefined) {
      console.log("2");

      if (arr.includes(data)) {
        const deleteIndex = arr.indexOf(data);
        arr.splice(deleteIndex, 1);
      } else {
        console.log("3");

        arr.push(data);
      }

      if (name === "services") {
        console.log("4");

        this.setState({
          services: arr
        });
      } else {
        this.setState({
          speciality: arr
        });
      }
    }
  }


  async saveData(e: FormEvent<HTMLFormElement>) {
    e.preventDefault();
    const {
      id,
      name,
      description,
      contact_number,
      email,
      start_price,
      end_price,
      services,
      speciality,
      location,
      insta_link,
      fb_link,
      start_working_hours,
      end_working_hours,
      vendor_id
    } = this.state;

    const data: MakeupArtistI = {
      id,
      name,
      description,
      contact_number,
      email,
      start_price,
      end_price,
      services,
      speciality,
      location,
      insta_link,
      fb_link,
      start_working_hours,
      end_working_hours,
      vendor_id
    };
    if (services.length === 0) {
      this.setState({
        error: "Enter atleat one service."
      });
      return;
    }
    try {
      const res = await MyHttp.post("/api/makeupArtist", data);
      if (res.status === 200) {
        console.log(res.data);
        this.setState({
          message: "Added successfully."
        });
        // @ts-ignore
        document!.getElementById("my-form")!.reset();
      } else {
        this.setState({
          error: res.data.message
        });
      }
    } catch (error) {
      this.setState({
        error: error.message
      });
    }
  }


  render() {
    return (
      <React.Fragment>
        <MyNavBar />
        <div className="container">
          <div className="jumbotron">
            <h1>Add Makeup Artist</h1>
            <form onSubmit={this.saveData} id="my-form">
              <div className="container-fluid">
                <div className="row align-items-start">
                  <div className="col">
                    <MyInput label="Name" name="name" onChange={this.onChange} />
                  </div>
                  <div className="col">
                    <MyInput
                      label="Contact Number"
                      name="contact_number"
                      onChange={this.onChange}
                    />
                  </div>

                  <div className="col">
                    <MyInput label="Email" name="email" onChange={this.onChange} />
                  </div>
                </div>
              </div>

              <MyTextArea
                label="Description"
                name="description"
                onChange={this.onChange}
              />

              <div className="container">
                <div className="row align-items-start">
                  <div className="col">
                    <MyInput
                      label="Start Price"
                      name="start_price"
                      onChange={this.onChange}
                    />
                  </div>
                  <div className="col">
                    <MyInput
                      label="End Price"
                      name="end_price"
                      onChange={this.onChange}
                    />
                  </div>
                </div>
              </div>
              <div className="container">
                <div className="row align-items-start">
                  <div className="col">
                    <label>Services:</label>
                    <select
                      onChange={this.onSelectChange}
                      name="services"
                      className="custom-select"
                      multiple
                    >
                      <option value="Bridal Makeup">Bridal Makeup</option>
                      <option value="Full Makeup">Full Makeup</option>
                      <option value="Face Makeup">Face Makeup</option>
                    </select>
                  </div>
                  <div className="col">
                    <label>Speciality:</label>
                    <select
                      onChange={this.onSelectChange}
                      name="speciality"
                      className="custom-select"
                      multiple
                    >
                      <option value="Bridal Makeup">Bridal Makeup</option>
                      <option value="Full Makeup">Full Makeup</option>
                      <option value="Face Makeup">Face Makeup</option>
                    </select>
                  </div>
                </div>
              </div>
              <div className="container-fluid">
                <div className="row align-items-start">
                  <div className="col">

                    <MyInput
                      label="Location"
                      name="location"
                      onChange={this.onChange}
                    />
                  </div>
                  <div className="col">

                    <MyInput
                      label="Insta Link"
                      name="insta_link"
                      type="link"
                      onChange={this.onChange}
                    />
                  </div>
                  <div className="col">

                    <MyInput
                      label="Fb Link"
                      name="fb_link"
                      type="link"
                      onChange={this.onChange}
                    />
                  </div>
                </div>
              </div>
              <div className="container-fluid">
                <div className="row align-items-start">
                  <div className="col">
                    <div>
                      <h4>
                        Monday
                </h4>
                    </div>
                    <MyInput
                      label="Start Working Hours"
                      type="time"
                      name="start_working_hours"
                      onChange={this.onChange}
                    />

                    <MyInput
                      label="End Working Hours"
                      type="time"
                      name="end_working_hours"
                      onChange={this.onChange}
                    />

                  </div>
                  <div className="col">
                    <div>
                      <h4>
                        Tuesday
                </h4>
                    </div>
                    <MyInput
                      label="Start Working Hours"
                      type="time"
                      name="start_working_hours"
                      onChange={this.onChange}
                    />

                    <MyInput
                      label="End Working Hours"
                      type="time"
                      name="end_working_hours"
                      onChange={this.onChange}
                    />
                  </div>
                  <div className="col">
                    <div>
                      <h4>
                        Wednesday
                </h4>
                    </div>
                    <MyInput
                      label="Start Working Hours"
                      type="time"
                      name="start_working_hours"
                      onChange={this.onChange}
                    />

                    <MyInput
                      label="End Working Hours"
                      type="time"
                      name="end_working_hours"
                      onChange={this.onChange}
                    />
                  </div>
                </div>
              </div>
              <div className="container-fluid">
                <div className="row align-items-start">
                  <div className="col">
                    <div>
                      <h4>
                        Thursday
                </h4>
                    </div>
                    <MyInput
                      label="Start Working Hours"
                      type="time"
                      name="start_working_hours"
                      onChange={this.onChange}
                    />

                    <MyInput
                      label="End Working Hours"
                      type="time"
                      name="end_working_hours"
                      onChange={this.onChange}
                    />

                  </div>
                  <div className="col">
                    <div>
                      <h4>
                        Friday
                </h4>
                    </div>
                    <MyInput
                      label="Start Working Hours"
                      type="time"
                      name="start_working_hours"
                      onChange={this.onChange}
                    />

                    <MyInput
                      label="End Working Hours"
                      type="time"
                      name="end_working_hours"
                      onChange={this.onChange}
                    />
                  </div>
                  <div className="col">
                    <div>
                      <h4>
                        Saturday
                </h4>
                    </div>
                    <MyInput
                      label="Start Working Hours"
                      type="time"
                      name="start_working_hours"
                      onChange={this.onChange}
                    />
                    <MyInput
                      label="End Working Hours"
                      type="time"
                      name="end_working_hours"
                      onChange={this.onChange}
                    />
                  </div>
                </div>
              </div>
              <div>
                <h4>
                  Sunday
                </h4>
              </div>
              <MyInput
                label="Start Working Hours"
                type="time"
                name="start_working_hours"
                onChange={this.onChange}
              />

              <MyInput
                label="End Working Hours"
                type="time"
                name="end_working_hours"
                onChange={this.onChange}
              />
              <button type="submit" className="btn btn-success">
                Submit
              </button>
              <p className="text-danger">{this.state.error}</p>
              <p className="text-success">{this.state.message}</p>
            </form>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
