import React from "react"

export interface MyInputI {
    name?: string
    value?: string
    onChange: ((event: React.ChangeEvent<HTMLInputElement>) => void) | undefined
    id?: string
    label?: string
    placeholder?: string
    type?: string
    onKeyDown?: ((event: React.KeyboardEvent<HTMLInputElement>) => void) | undefined
    required?: boolean
}

const MyInput = (props: MyInputI) => {

    return (
        <React.Fragment>
            <div className="form-group">
                <label htmlFor={props.id}>{props.label}:</label>
                <input  type={(props.type) ? props.type : "text"} onKeyDown={props.onKeyDown}  id={props.id} name={props.name} value={props.value} onChange={props.onChange} placeholder={props.placeholder} className="form-control"  required={(props.required !== undefined) ? props.required : true} />
            </div>
            <br />
        </React.Fragment>
    )
}

export default MyInput
