import React, { Component, FormEvent, ChangeEvent } from 'react'

import MyNavBar from "../../common/MyNavBar"
import MyInput from "../../common/MyInput"
import { Form } from "react-bootstrap"
import MyTextArea from "../../common/MyTextarea"
import MyHttp from "../../helper/MyHttp"
import EventI from '../event/event-interface'

export interface DesignersI {
    id?: number | null
    brand_name: string
    designer_name: string
    contact_number: string
    email: string
    start_price: number
    end_price: number
    outfit_types: string[]
    speciality: string
    location: string
    insta_link?: string
    fb_link?: string
    start_working_hours: Date | string
    end_working_hours: Date | string
    photo_ids?: number[]
    approved?: boolean
    vendor_id?: number
    events?: EventI[]
}
interface AddDesignerI extends DesignersI {
    outfit_type_text: string
    error: string
    message: string
}

export default class AddFashionDesigner extends Component<any, AddDesignerI> {

    constructor(props: any) {
        super(props)

        this.state = {
            brand_name: "",
            designer_name: "",
            contact_number: "",
            email: "",
            start_price: 0,
            end_price: 0,
            outfit_types: [],
            outfit_type_text: "",
            speciality: "",
            location: "",
            insta_link: "",
            fb_link: "",
            start_working_hours: "",
            end_working_hours: "",
            vendor_id: this.props.match.params.id,
            error: "",
            message: ""
        }
        this.saveData = this.saveData.bind(this)
        this.onChange = this.onChange.bind(this)
        this.onOutfitAdded = this.onOutfitAdded.bind(this)
        this.renderOutfits = this.renderOutfits.bind(this)
    }

    onChange(e: React.ChangeEvent<HTMLInputElement>) {
        console.log(e.currentTarget.value);

        this.setState({
            ...this.state,
            [e.currentTarget.name]: e.currentTarget.value
        })
    }

    onOutfitAdded(event: React.KeyboardEvent<HTMLInputElement>) {
        if (event.keyCode === 13) {
            const { outfit_type_text, outfit_types } = this.state
            outfit_types.push(event.currentTarget.value)
            this.setState({
                outfit_types
            })
            event.currentTarget.value = ""
            event.preventDefault()
        }

    }

    async saveData(e: FormEvent<HTMLFormElement>) {
        e.preventDefault()
        const {
            brand_name,
            designer_name,
            contact_number,
            email,
            start_price,
            end_price,
            outfit_types,
            speciality,
            location,
            insta_link,
            fb_link,
            start_working_hours,
            end_working_hours,
            vendor_id
        } = this.state

        const data: DesignersI = {
            brand_name,
            designer_name,
            contact_number,
            email,
            start_price,
            end_price,
            outfit_types,
            speciality,
            location,
            insta_link,
            fb_link,
            start_working_hours,
            end_working_hours,
            vendor_id
        }
        if(outfit_types.length === 0){
            this.setState({
                error: "Enter atleat one outfit type."
            })
            return 
        }
        try {
            const res = await MyHttp.post("/api/designer", data)
            if (res.status === 200) {
                console.log(res.data);
                this.setState({
                    message: 'Added successfully.'
                })
                // @ts-ignore
                document!.getElementById('my-form')!.reset()
            } else {
                this.setState({
                    error: res.data.message
                })
            }

        } catch (error) {
            this.setState({
                error: error.message
            })
        }
    }

    renderOutfits(): React.ReactNode {
        const { outfit_types } = this.state
        const out = outfit_types?.map((ot: string, i: number) => {
            return <li key={ot}>{ot} <button className="btn btn-sm btn-secondary" onClick={() => {
                outfit_types.splice(i, 1)
                this.setState({
                    outfit_types
                })
            }} > - </button></li>
        })
        return out
    }


    render() {
        return (
            <React.Fragment>
                <MyNavBar />
                <div className="container">
                    <div className="jumbotron">
                        <h1>Add</h1>
                        <form onSubmit={this.saveData} id="my-form">

                            <MyInput label="Brand Name" name="brand_name" onChange={this.onChange} />
                            <MyInput label="Designer Name" name="designer_name" onChange={this.onChange} />
                            <MyInput label="Contact Number" name="contact_number" onChange={this.onChange} />
                            <MyInput label="Email" name="email" onChange={this.onChange} />
                            <MyInput label="Start Price" name="start_price" onChange={this.onChange} />
                            <MyInput label="End Price" name="end_price" onChange={this.onChange} />
                            <MyInput label="Outfit Types (hit enter to add)" name="outfit_type_text" required={false} onKeyDown={this.onOutfitAdded} onChange={this.onChange}/>
                            <ol>
                                {this.renderOutfits()}
                            </ol>
                            <MyInput label="Speciality" name="speciality" onChange={this.onChange} />
                            <MyInput label="Location" name="location" onChange={this.onChange} />
                            <MyInput label="Insta Link" name="insta_link" type="link" onChange={this.onChange} />
                            <MyInput label="Fb Link" name="fb_link" type="link" onChange={this.onChange} />
                            <MyInput label="Start Working Hours" type="time"  name="start_working_hours" onChange={this.onChange} />
                            <MyInput label="End Working Hours" type="time" name="end_working_hours" onChange={this.onChange} />
                            <button type="submit" className="btn btn-success">Submit</button>
                            <p className="text-danger">{this.state.error}</p>
                            <p className="text-success">{this.state.message}</p>

                        </form>

                    </div>
                </div>
            </React.Fragment>
        )
    }

}
