import React from 'react';
import './App.css';
import { Switch, Route, Router } from 'react-router-dom';
import Login from './components/pages/login';
import CreateAdmin from './components/pages/admin/create-admin';
import Home from './components/pages/home';
import Logout from './components/pages/logout';
import ViewAdmin from './components/pages/admin/view-admin';
import AddEvent from './components/pages/event/add-event';
import history from './components/history';
import ViewEvent from './components/pages/event/view-event';
import AddVendor from './components/pages/vendor/add-vendor';
import ViewVendor from './components/pages/vendor/view-vendor';
import VendorDetail from './components/pages/vendor/vendor-detail';
import AddFashionDesigner from './components/pages/fashion-designer/add-fashion-designer';
import ViewFashionDesigner from './components/pages/fashion-designer/view-fashion-designer';
import AddMakeupartist from './components/pages/makeup-artist/add-makeup-artist';
import ViewMakeupArtist from './components/pages/makeup-artist/view-makeup-artist';

function App() {
  
  return (
    <Router history={history}>
      <Route exact path="/" component={Login} />
      <Route exact path="/home" component={Home} />
      <Route exact path="/logout" component={Logout} />
      <Route exact path="/admin/create" component={CreateAdmin} />
      <Route exact path="/admin/view" component={ViewAdmin} />
      <Route exact path="/event/create" component={AddEvent} />
      <Route exact path="/event/view" component={ViewEvent} />
      <Route exact path="/vendor/create" component={AddVendor} />
      <Route exact path="/vendor/view" component={ViewVendor} />
      <Route exact path="/vendor/detail/:id" component={VendorDetail} />
      <Route exact path="/designer/create/:id" component={AddFashionDesigner} />
      <Route exact path="/makeupartist/create/:id" component={AddMakeupartist} />
      <Route exact path="/designer/view/:id" component={ViewFashionDesigner} />
      <Route exact path="/makeupartist/view/:id" component={ViewMakeupArtist} />
    </Router>
  );
}

export default App;
