import React, { Component } from "react";
import { MakeupArtistI } from "./add-makeup-artist";
import MyHttp from "../../helper/MyHttp";
import MyNavBar from "../../common/MyNavBar";

interface ViewMakeupArtistI extends MakeupArtistI {
  error: string;
  loading: boolean;
}

export default class ViewMakeupArtist extends Component<
  any,
  ViewMakeupArtistI
> {
  constructor(props: any) {
    super(props);

    this.state = {
      id: this.props.match.params.id,
      name: "",
      description: "",
      contact_number: "",
      email: "",
      start_price: 0,
      end_price: 0,
      services: [],
      speciality: [],
      location: "",
      insta_link: "",
      fb_link: "",
      start_working_hours: "",
      end_working_hours: "",
      vendor_id: 0,
      error: "",
      loading: false,
    }
  }

  async getData() {
    try {
      const res = await MyHttp.get(`/api/makeupArtist/${this.state.id}`);

      if (res.status === 200) {
        console.log(res.data);
        if (res.data !== null) {
          const data: MakeupArtistI = res.data;
          this.setState({
            name: data.name,
            description: data.description,
            contact_number: data.contact_number,
            email: data.email,
            start_price: data.start_price,
            end_price: data.end_price,
            services: data.services,
            speciality: data.speciality,
            location: data.location,
            insta_link: data.insta_link,
            fb_link: data.fb_link,
            start_working_hours: data.start_working_hours,
            end_working_hours: data.end_working_hours
          });
        }
      }
    } catch (error) {
      this.setState({
        error: error.message
      });
    }
  }

  componentDidMount(){
      this.getData()
  }

  render() {
    return (
      <React.Fragment>
          <MyNavBar />
        <div className="container">
          <div className="jumbotron">
            <h3>Name</h3>
            <p>{this.state.name}</p>
            <h3>Description</h3>
            <p>{this.state.description}</p>
            <h3>Contact Number</h3>
            <p>{this.state.contact_number}</p>
            <h3>Email</h3>
            <p>{this.state.email}</p>
            <h3>Start Price</h3>
            <p>{this.state.start_price}</p>
            <h3>End Price</h3>
            <p>{this.state.end_price}</p>
            <h3>Services</h3>
            <p>{this.state.services}</p>
            <h3>Speciality</h3>
            <p>{this.state.speciality}</p>
            <h3>Location</h3>
            <p>{this.state.location}</p>
            <h3>Insta Link</h3>
            <p>{this.state.insta_link}</p>
            <h3>Fb Link</h3>
            <p>{this.state.fb_link}</p>
            <h3>Start Working Hours</h3>
            <p>{this.state.start_working_hours}</p>
            <h3>End Working Hours</h3>
            <p>{this.state.end_working_hours}</p>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
