import React, { Component, FormEvent } from 'react'
import MyNavBar from '../../common/MyNavBar'
import MyInput from '../../common/MyInput'
import MyTextArea from '../../common/MyTextarea'
import MyHttp from '../../helper/MyHttp'
import { DesignersI } from '../fashion-designer/add-fashion-designer'
import { MakeupArtistI } from '../makeup-artist/add-makeup-artist'


export interface VendorI{
    id?: number | null
    name: string
    email: string
    password?: string
    contact_number: string
    premium?: boolean
    designers?: DesignersI[]
    makeupArtist?: MakeupArtistI[]
    // salons?: SalonI[]
}

interface AddVendorI extends VendorI{
    error: string
    message: string
}

export default class AddVendor extends Component<any, AddVendorI> {
    constructor(props: any) {
        super(props)
    
        this.state = {
            name: "",
            email: "",
            password: "",
            contact_number: "",
            premium: false,
            error: "",
            message: ""
        }

        this.saveData = this.saveData.bind(this)
        this.onChange = this.onChange.bind(this)
    }

    onChange(e: React.ChangeEvent<HTMLInputElement>) {
        console.log(e.currentTarget.value);
        
        this.setState({
            ...this.state,
            [e.currentTarget.name]: e.currentTarget.value
        })
    }

    async saveData(e: FormEvent<HTMLFormElement>) {
        e.preventDefault()
        const {name, email, password, contact_number} = this.state

        const data: VendorI = {
            name, email, password, contact_number
        }
        try {
            const res = await MyHttp.post("/api/vendor", data)
            if (res.status === 200) {
                console.log(res.data);
                this.setState({
                    message:'Added successfully.'
                })
                // @ts-ignore
                document!.getElementById('my-form')!.reset()
            } else {
                this.setState({
                    error: res.data.message
                })
            }

        } catch (error) {
            this.setState({
                error: error.message
            })
        }
    }

    
    render() {
        return (
            <React.Fragment>
            <MyNavBar />
            <div className="container">
                <div className="jumbotron">
                    <h1>Add</h1>
                    <form onSubmit={this.saveData} id="my-form">

                        <MyInput label="Name" name="name" onChange={this.onChange} />
                        <MyInput label="Email" name="email" onChange={this.onChange} />
                        <MyInput label="Password" name="password" type="password" onChange={this.onChange} />
                        <MyInput label="Contact Number" name="contact_number" onChange={this.onChange} />
                        
                        <button type="submit" className="btn btn-success">Submit</button>
                        <p className="text-danger">{this.state.error}</p>
                        <p className="text-success">{this.state.message}</p>

                    </form>

                </div>
            </div>
        </React.Fragment>
        )
    }
}
