import React, { useEffect, useState } from "react"
import MyNavBar from "../../common/MyNavBar"
import MyHttp from "../../helper/MyHttp"
import MyInput from "../../common/MyInput"
import MyRadioBtn from "../../common/MyRadioBtn"
import { Modal, Button } from "react-bootstrap"
import MyTextArea from "../../common/MyTextarea"
import  moment from 'moment';
import { VendorI } from "./add-vendor"
import { Link } from "react-router-dom"

interface ViewVendorI extends VendorI {
    data: VendorI[]
    show: boolean
    error: string
}

class ViewVendor extends React.Component<any, ViewVendorI> {


    constructor(props: any) {
        super(props)

        this.state = {
            data: [],
            id: 0,
            name: "",
            email: "",
            contact_number: "",
            show: false,
            error: ""
        }

        this.handleClose = this.handleClose.bind(this)
        this.saveData = this.saveData.bind(this)
        this.onChange = this.onChange.bind(this)
    }


    async getData() {
        try {
            const res = await MyHttp.get("/api/vendor")

            if (res.status === 200) {
                console.log(res.data);
                if(res.data !== null){
                    const data: VendorI[] = res.data
                    this.setState({
                        data: res.data
                    })
                }
            }
        } catch (error) {
            this.setState({
                error: error.message
            })
        }
    }

    setEditData(id: number) {
        this.setState({
            id: id
        })
        const gotData: VendorI | undefined = this.state.data.find((d: VendorI) => d.id === id)
        if (gotData === undefined || gotData === null) {
            console.log("Wrong Id");
            return
        }

        this.setState({
            show: true,
            name: gotData.name,
            email: gotData.email,
            contact_number: gotData.contact_number
        })

    }

    async  saveData() {
        try {
            const dataToSend: VendorI = {
                id: this.state.id,
                name: this.state.name,
                email: this.state.email,
                contact_number: this.state.contact_number
            }
            const res = await MyHttp.put("/api/vendor", dataToSend)
            const gotData: VendorI = res.data
            const dataIndex: number = this.state.data.findIndex((d: VendorI) => d.id === gotData.id)
            console.log(gotData);

            this.state.data[dataIndex] = gotData
            console.log(this.state.data);
            this.setState({
                data: this.state.data,
                show: false
            })
            console.log(this.state.data);

        } catch (error) {
            console.log(error.message);

        }
    }

    renderData() {
        const out = (this.state.data || []).map((d: VendorI, i: number) => {
            const linkTo = `/vendor/detail/${d.id}`
            return (
                <tr key={d.id!}>
                    <td>{i + 1}</td>
                    <td> <Link to={linkTo}>  {d.name} </Link> </td>
                    <td>{d.email}</td>
                    <td>{d.contact_number}</td>
                    <td><button className="btn btn-warning" onClick={() => {
                        this.setEditData(d.id!)

                    }}>Edit</button></td>
                </tr>
            )
        })
        return out
    }

    componentDidMount() {
        this.getData()
    }

    handleClose() {
        this.setState({
            show: false
        })
    }

    onChange(e: React.ChangeEvent<HTMLInputElement>) {
        console.log(e.currentTarget.value);
        
        this.setState({
            ...this.state,
            [e.currentTarget.name]: e.currentTarget.value
        })
    }

    render() {
        return (
            <React.Fragment>
                <MyNavBar />
                <div className="container">
                    <div className="jumbotron">
                        <h1>View/Edit</h1>
                        <Modal show={this.state.show} onHide={this.handleClose}>
                            <Modal.Header closeButton>
                                <Modal.Title>Modal heading</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <MyInput label="Name" name="name" value={this.state.name} onChange={this.onChange} />
                                <MyInput label="Email" name="email" value={this.state.email} onChange={this.onChange} />
                                <MyInput label="Contact Number" name="contact_number" value={this.state.contact_number} onChange={this.onChange} />
                            </Modal.Body>
                            <Modal.Footer>
                                <Button variant="secondary" onClick={this.handleClose}>
                                    Close
                                </Button>
                                <Button variant="primary" onClick={this.saveData}>
                                    Save Changes
                                </Button>
                            </Modal.Footer>
                        </Modal>
                        <table className="table">
                            <thead>
                                <tr>
                                    <th scope="col">#</th>
                                    <th scope="col">Name</th>
                                    <th scope="col">Email</th>
                                    <th scope="col">Contact Number</th>
                                    <th scope="col">Edit</th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.renderData()}
                            </tbody>
                        </table>
                    </div>
                    <p className="text-danger">{this.state.error}</p>
                </div>
            </React.Fragment >

        )
    }

}

export default ViewVendor