import React, { useEffect, useState } from "react";
import MyNavBar from "../../common/MyNavBar";
import MyHttp from "../../helper/MyHttp";
import MyInput from "../../common/MyInput";
import MyRadioBtn from "../../common/MyRadioBtn";
import { Modal, Button } from "react-bootstrap";
import MyTextArea from "../../common/MyTextarea";
import moment from "moment";
import { VendorI } from "./add-vendor";
import { Link } from "react-router-dom";
import { DesignersI } from "../fashion-designer/add-fashion-designer";
import { MakeupArtistI } from "../makeup-artist/add-makeup-artist";

interface ViewVendorI extends VendorI {
  data: VendorI | null;
  show: boolean;
  error: string;
}

class VendorDetail extends React.Component<any, ViewVendorI> {
  constructor(props: any) {
    super(props);

    this.state = {
      data: null,
      id: this.props.match.params.id,
      name: "",
      email: "",
      contact_number: "",
      designers: [],
      show: false,
      error: ""
    };

    this.handleClose = this.handleClose.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  async getData() {
    if (!this.state.id) return;
    try {
      const res = await MyHttp.get(`/api/vendor/${this.state.id}`);

      if (res.status === 200) {
        console.log(res.data);
        if (res.data !== null) {
          const data: VendorI = res.data;
          this.setState({
            name: data.name,
            email: data.email,
            contact_number: data.contact_number,
            designers: data.designers,
            makeupArtist: data.makeupArtist
          });
        }
      }
    } catch (err) {
      
    }
  }




  componentDidMount() {
    this.getData();
  }

  handleClose() {
    this.setState({
      show: false
    });
  }

  onChange(e: React.ChangeEvent<HTMLInputElement>) {
    console.log(e.currentTarget.value);

    this.setState({
      ...this.state,
      [e.currentTarget.name]: e.currentTarget.value
    });
  }

  renderFashionDesigner() {
    const out = (this.state.designers || []).map((d: DesignersI, i: number) => {
      const to = `/designer/view/${d.id}`;
      return (
        <tr key={d.id!}>
          <td>{i + 1}</td>
          <td>
            <Link to={to}>{d.brand_name}</Link>
          </td>
          <td>{d.email}</td>
          <td>{d.contact_number}</td>
        </tr>
      );
    });
    return out;
  }

  renderMakeupartist() {
    const out = (this.state.makeupArtist || []).map(
      (d: MakeupArtistI, i: number) => {
        const to = `/makeupartist/view/${d.id}`;
        return (
          <tr key={d.id!}>
            <td>{i + 1}</td>
            <td>
              <Link to={to}>{d.name}</Link>
            </td>
            <td>{d.email}</td>
            <td>{d.contact_number}</td>
          </tr>
        );
      }
    );
    return out;
  }

  saveData(){

  }

  render() {
    return (
      <React.Fragment>
        <MyNavBar />
        <div className="container">
          <div className="jumbotron">
            <Modal show={this.state.show} onHide={this.handleClose}>
              <Modal.Header closeButton>
                <Modal.Title>Modal heading</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <MyInput
                  label="Name"
                  name="name"
                  value={this.state.name}
                  onChange={this.onChange}
                />
                <MyInput
                  label="Email"
                  name="email"
                  value={this.state.email}
                  onChange={this.onChange}
                />
                <MyInput
                  label="Contact Number"
                  name="contact_number"
                  value={this.state.contact_number}
                  onChange={this.onChange}
                />
              </Modal.Body>
              <Modal.Footer>
                <Button variant="secondary" onClick={this.handleClose}>
                  Close
                </Button>
                <Button variant="primary" onClick={this.saveData}>
                  Save Changes
                </Button>
              </Modal.Footer>
            </Modal>
            <h2>
              #{this.state.id ?? 0} {this.state.name}
            </h2>
            <p>
              {this.state.email} {this.state.contact_number}
            </p>
          </div>
          <div>
            <h2 style={{ float: "left" }}>Fashion Designer</h2>
            <Link
              style={{ float: "right" }}
              className="btn btn-success"
              to={`/designer/create/${this.state.id}`}
            >
              +
            </Link>
            <table className="table">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Brand Name</th>
                  <th scope="col">Email</th>
                  <th scope="col">Contact Number</th>
                </tr>
              </thead>
              <tbody>{this.renderFashionDesigner()}</tbody>
            </table>
          </div>
          <div>
            <h2 style={{ float: "left" }}>Makeup Artist</h2>
            <Link
              style={{ float: "right" }}
              className="btn btn-success"
              to={`/makeupartist/create/${this.state.id}`}
            >
              +
            </Link>
            <table className="table">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Name</th>
                  <th scope="col">Email</th>
                  <th scope="col">Contact Number</th>
                </tr>
              </thead>
              <tbody>{this.renderMakeupartist()}</tbody>
            </table>
          </div>
          <div>
            <h2 style={{ float: "left" }}>Salons</h2>
            <Link
              style={{ float: "right" }}
              className="btn btn-success"
              to={`/makeupartist/create/${this.state.id}`}
            >
              +
            </Link>
            <table className="table">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Name</th>
                  <th scope="col">Email</th>
                  <th scope="col">Contact Number</th>
                </tr>
              </thead>
              <tbody>{this.renderMakeupartist()}</tbody>
            </table>
          </div>
          <p className="text-danger">{this.state.error}</p>
        </div>
      </React.Fragment>
    );
  }
}

export default VendorDetail;
